import React, { Component } from "react";
import ReactDataGrid from "react-data-grid";
import { withStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import Modal from "react-modal";
import logo from "./logo.svg";
import "./App.css";

const styles = theme => ({
  progress: {
    margin: theme.spacing.unit * 2
  }
});

const columns = [
  {
    key: "id",
    name: "ID",
    resizable: true,
    width: 40
  },
  {
    key: "first_name",
    name: "FirstName",
    resizable: true,
    events: {
      onClick: function(ev, args) {
        console.log(
          `The user entered edit mode on title column with rowIdx: ${
            args.rowIdx
          } & rowId: ${args.rowId}`,
          this.modalIsOpen(args)
        );
      }
    }
  },
  {
    key: "last_name",
    name: "LastName",
    resizable: true,
    events: {
      onClick: function(ev, args) {
        console.log(
          `The user entered edit mode on title column with rowIdx: ${
            args.rowIdx
          } & rowId: ${args.rowId}`,
          this.modalIsOpen(args)
        );
      }
    }
  },
  {
    key: "email",
    name: "Email",
    resizable: true
  },
  {
    key: "gender",
    name: "Gender",
    resizable: true
  },
  {
    key: "company",
    name: "Company",
    resizable: true
  }
];

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)"
  }
};

class App extends Component {
  state = {
    loading: false,
    users: [],
    modalIsOpen: false
  };

  openModal() {
    this.setState({ modalIsOpen: true });
  }

  afterOpenModal() {
    // references are now sync'd and can be accessed.
    this.subtitle.style.color = "#f00";
  }

  closeModal() {
    this.setState({ modalIsOpen: false });
  }

  componentDidMount() {
    // 测试 devServer 的代理功能
    fetch("/api/clients")
      .then(resp => resp.json())
      .then(res => this.setState({ users: res }));
  }

  rowGetter = i => this.state.users[i];

  render() {
    return (
      <div className="App">
        <Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.afterOpenModal}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Row Modal"
        />
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to Grid Challange</h2>
        </div>
        <div className="container">
          {this.state.users ? (
            <ReactDataGrid
              columns={columns}
              rowGetter={this.rowGetter}
              rowsCount={this.state.users.length}
              minHeight={500}
              minColumnWidth={120}
            />
          ) : (
            <CircularProgress />
          )}
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(App);



